@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
section[class*=-section].section{width:100%;display:block;min-height:300px}section[class*=-section].section div[class*=-container].container{max-width:1460px;padding:20px;margin:0 auto}
.navigation-section{position:relative;background:#1e3250;color:#fff}.navigation-section .main-navbar{display:flex;justify-content:space-between}.navigation-section .main-navbar .navbar-brand{padding:5px;font-weight:bold;color:#fff;font-size:32px}.navigation-section .main-navbar .navbar-brand p{font-size:16px;font-style:italic}.navigation-section .main-navbar .navbar-list{display:flex;grid-gap:20px;gap:20px;list-style-type:none;margin:0}.navigation-section .main-navbar .navbar-list .navbar-list-item{border-top:2px solid transparent;font-weight:bold}.navigation-section .main-navbar .navbar-list .navbar-list-item a{color:#fff;font-size:24px;height:100%;display:block}.navigation-section .main-navbar .navbar-list .navbar-list-item:hover{border-top:2px solid #fff}@media(max-width: 1024px){.navigation-section .main-navbar .navbar-list{flex-direction:column}}
.bottom-divider{position:absolute;bottom:0;left:0;width:100%;overflow:hidden;line-height:0;-webkit-transform:rotate(180deg);transform:rotate(180deg)}.bottom-divider svg{position:relative;display:block;width:calc(300% + 1.3px);height:92px;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.bottom-divider .shape-fill.dark-fill{fill:#1e3250}.bottom-divider .shape-fill.light-fill{fill:#fff}
.top-divider{position:absolute;top:0;left:0;width:100%;overflow:hidden;line-height:0}.top-divider svg{position:relative;display:block;width:calc(300% + 1.3px);height:129px;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.top-divider .shape-fill.dark-fill{fill:#1e3250}.top-divider .shape-fill.light-fill{fill:#fff}
.contacts-section.section{position:relative;min-height:200px;background:#1e3250;color:#fff;padding-top:100px}.contacts-section.section .contacts-container.container .main-footer{text-align:center;color:#fff;position:relative}.contacts-section.section .contacts-container.container .main-footer h3{text-align:right;font-size:32px}.contacts-section.section .contacts-container.container .main-footer ul.contacts{display:flex;justify-content:space-between}.contacts-section.section .contacts-container.container .main-footer ul.contacts .contact-icon{width:60px;height:60px}.contacts-section.section .contacts-container.container .main-footer ul.contacts li{list-style-type:none;margin:55px 0}.contacts-section.section .contacts-container.container .main-footer ul.contacts li .external-link{display:block;color:#fff}.contacts-section.section .contacts-container.container .main-footer ul.contacts li .external-link:hover{font-weight:bold}
.about-us-section.section .about-us-container.container h3{font-size:32px}.about-us-section.section .about-us-container.container .about-us{display:flex}.about-us-section.section .about-us-container.container .about-us .about-us-text{width:100%;font-size:20px;padding:20px 0;text-justify:newspaper}.about-us-section.section .about-us-container.container .about-us .about-us-text p{margin-bottom:10px}.about-us-section.section .about-us-container.container .about-us .about-us-text p .bold{font-weight:bold}.about-us-section.section .about-us-container.container .about-us .about-us-text ul.our-services-list{list-style-position:inside;list-style-type:none}.about-us-section.section .about-us-container.container .about-us .about-us-text ul.our-services-list li{display:flex;align-items:center;grid-gap:15px;gap:15px}.about-us-section.section .about-us-container.container .about-us .about-us-pictures{width:100%}.about-us-section.section .about-us-container.container .about-us .about-us-pictures img{display:block;width:100%;box-shadow:0px 0px 15px 1px rgba(30,50,90,.55);border-radius:5px}@media(max-width: 1024px){.about-us-section.section .about-us-container.container .about-us{display:block}.about-us-section.section .about-us-container.container .about-us .about-us-pictures{width:75%;margin:0 auto}.about-us-section.section .about-us-container.container .about-us .about-us-text{margin:0 auto}}
.excursions-service-section.section{position:relative;background:#1e3250;color:#fff;padding:100px 0}.excursions-service-section.section .excursions-service-container.container{display:flex;flex-direction:column}.excursions-service-section.section .excursions-service-container.container h3{text-align:right;font-size:32px}.excursions-service-section.section .excursions-service-container.container .trip-quote{font-size:24px}.excursions-service-section.section .excursions-service-container.container .excursions{display:flex}
.country-list{margin-top:45px;display:flex;justify-content:space-between;flex-wrap:wrap}.country-list .country-list-item{display:flex;grid-gap:5px;gap:5px;align-items:center;margin-bottom:15px}.country-list .country-list-item .country-img-container{max-width:50px;width:100%}.country-list .country-list-item .country-img-container .country-img{display:block;width:100%}
.trip-image-list{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}.trip-image-list .trip-image{transition:all .3s ease-in-out;border-radius:5px}.trip-image-list .trip-image.first-row{margin:30px 0}.trip-image-list .trip-image:hover{cursor:pointer;-webkit-clip-path:polygon(20% 0%, 80% 0%, 100% 0%, 100% 80%, 80% 100%, 0% 100%, 0% 86%, 0% 20%);clip-path:polygon(20% 0%, 80% 0%, 100% 0%, 100% 80%, 80% 100%, 0% 100%, 0% 86%, 0% 20%);-webkit-transform:scale(1.05);transform:scale(1.05)}
.cargo-service-section.section{position:relative;background:#1e3250;color:#fff;padding-bottom:100px}.cargo-service-section.section .cargo-service-container.container h3{font-size:32px}.cargo-service-section.section .cargo-service-container.container .cargo-services .truck-types-list{display:flex;justify-content:space-between;list-style-type:none;margin:15px 0}.cargo-service-section.section .cargo-service-container.container .cargo-services .truck-types-list .truck-type-list-item{justify-content:center;align-items:center;display:flex;flex-direction:column}.cargo-service-section.section .cargo-service-container.container .cargo-services .truck-types-list .truck-type-list-item svg{width:3em;height:3em}.cargo-service-section.section .cargo-service-container.container .cargo-services .trucks-list{display:flex;justify-content:space-between;flex-wrap:wrap}.cargo-service-section.section .cargo-service-container.container .cargo-services .trucks-list img{transition:all .3s ease-in-out;border-radius:5px}.cargo-service-section.section .cargo-service-container.container .cargo-services .trucks-list img:hover{cursor:pointer;-webkit-clip-path:polygon(20% 0%, 80% 0%, 100% 0%, 100% 80%, 80% 100%, 0% 100%, 0% 86%, 0% 20%);clip-path:polygon(20% 0%, 80% 0%, 100% 0%, 100% 80%, 80% 100%, 0% 100%, 0% 86%, 0% 20%);-webkit-transform:scale(1.05);transform:scale(1.05)}
.our-customers-section.section .our-customers-container.container h3{font-size:32px}.our-customers-section.section .our-customers-container.container .our-clients-list{display:flex;justify-content:space-between;flex-wrap:wrap}@media(max-width: 1024px){.our-customers-section.section .our-customers-container.container .our-clients-list{justify-content:center;grid-gap:20px;gap:20px}}
.customer-list-item{list-style-type:none;text-align:center;max-width:400px;width:100%;box-shadow:0px 0px 15px 1px rgba(30,50,90,.33);border-radius:10px;margin-bottom:10px;min-height:170px}.customer-list-item a{display:flex;flex-direction:column;align-content:center;width:100%;height:100%;color:#1e3250;list-style-type:none;padding:5px;position:relative}.customer-list-item a p.customer-name{font-weight:bold;position:absolute;top:0;left:0;width:100%;display:none;font-size:32px;z-index:10}.customer-list-item a div.customer-image-container{height:100%;display:flex;align-items:center;justify-content:center;z-index:9}.customer-list-item a div.customer-image-container img.customer-image{align-self:center;max-width:150px;width:100%;display:block;margin:0 auto;z-index:8}.customer-list-item a:hover p.customer-name{display:block}.customer-list-item a:hover img.customer-image{filter:blur(5px);-webkit-filter:blur(5px)}
*{margin:0;padding:0}html{font-family:"Nunito",sans-serif;font-size:16px/1.5;box-sizing:border-box;scroll-behavior:smooth}body{background-color:#fff;color:#1e3250}body a{text-decoration:none}
